body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: black; 
  display: flex;
  height: 1300px;
}

.RightMenu{
    width: 100%;
    background-color:#db2828;
    padding: 10px;
    position: absolute;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    
    }
.Summary{
    font-size: 2.5vw;
}

.article{
    font-size: larger;
}
.header{
    padding-right: 10px;
}
.test{
    padding-left: 5px;
}
.name{
    font-size: large;
}
.info{
    font-size: medium;
}
.stats{
    font-size: large;
    color: black;
}
.roster{
    padding-top: 5px;
}
.label{
    font-size: 80px;
}


.playerName{
    font-size: 2vw;
}
.TeamInfo{
    padding-bottom: 10px;
}
.Title{
    color: #db2828;
    font-size: 5vw;
}
.options{
    padding-bottom: 5px;
    
}
.HomeButton{
    font-size: 2.75vw;


}
.Coach{
    font-size: 40px;
    color: black;
    font-weight: bolder;
}

.job{
    font-size: x-large;
    color: black;
    font-style: italic;
}
.Welcome{
    font-size: 50px;
    color: red;
    line-height: 1;
}

.description{
    font-size: x-large;
}

.features{
    font-size: large;
}
.motto{
    font-size: xx-large;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    word-spacing: 1px;
}

